// const URL = 'http://localhost:9080/api';
const URL = 'http://editor.gopopon.com/api';

const STATIC = false;

let API_URL = {

};

if (!STATIC) {
    API_URL = {
        // domain: 'http://localhost:8080/dist/upload/',
        domain: 'http://editor.gopopon.com/upload/',
        course: {
            save: `${URL}/course`,
            show: `${URL}/course/`,
        },
        template: {
            save: `${URL}/template`,
            list: `${URL}/templates`,
            listSearch: `${URL}/templates/listSearch`,
            show: `${URL}/template/`,
        },
        category: {
            list: `${URL}/categories`,
        },
        audio: {
            list: `${URL}/musics/all`,
            upload: `${URL}/upload/music`,
        },
        user: {
            login: `${URL}/user/login`,
        },
        image: {
            list: `${URL}/images`,
            upload: `${URL}/upload/image`,
            crop: `${URL}/image/crop`,
            del: `${URL}/image/`,
        },
    };
}

export { API_URL as default };



